.total-expenses {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 1rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.total-expenses__title {
  font-weight: lighter;
  font-size: 1.5rem;
  color: wheat;
}

.total-expenses__amount {
  font-weight: lighter;
  font-size: 2rem;
  color: white;
  font-weight: bold;
}
