@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}

.expenses {
  background: rgb(31, 31, 31);
  margin: 2rem auto;
  /* width: 50rem; */
  max-width: 95%;
  padding: 1rem;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}
.expense-item__date {
  font-size: 1.3rem;
  color: #fff;
}

.remove-icon {
  /* padding: 1rem; */
  border-radius: 12px;
  padding: 0rem 1.5rem;
  font-size: 2rem;
  background-color: transparent;
  outline: none;
  box-shadow: none;
  color: orange;
  cursor: pointer;
}

.expense-item__flex {
  display: flex;
  /* align-items: center; */
  justify-content: center;
  grid-gap: 0.7rem;
  gap: 0.7rem;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item__price {
  background: purple;
  color: #fff;
  font-size: 1.7rem;
  padding: 1rem 4rem;
  border: 1px solid #fff;
  border-radius: 15px;
}

@media (max-width: 800px) {
  .expense-item__description {
    flex-direction: row;
    align-content: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
  }
}

.card {
  border-radius: 12px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
}

.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}

.month-year {
  display: flex;
  align-items: center;
}

.month-year .month {
  margin-right: 1rem;
}

.month-year button {
  font: inherit;
  cursor: pointer;
  padding: 0.7rem 1rem;
  border: 1px solid #40005d;
  background-color: #f9dffe;
  border-radius: 12px;
  margin-left: 1rem;
}

.expenses-list {
  list-style: none;
  padding: 0;
}

.expenses-list__fallback {
  color: white;
  text-align: center;
}

.expense-date {
  display: flex;
  padding: 1.5rem;
  color: #fff;
  flex-direction: column;
  align-items: center;
  border-radius: 12px;
  border: 1px solid #fff;
  background: rgba(0, 0, 0, 0.4);
}

.expense-date__month {
  font-weight: bold;
  font-size: 1.2rem;
}

.expense-date__year {
  font-size: 1.1rem;
}

.expense-date__day {
  font-size: 2rem;
  font-weight: 700;
  margin-top: 5px;
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.6rem;
  text-align: center;
}

.chart {
  padding: 1rem;
  border-radius: 12px;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
  background-color: #f8dfff;
}

.total-expenses {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 1rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.total-expenses__title {
  font-weight: lighter;
  font-size: 1.5rem;
  color: wheat;
}

.total-expenses__amount {
  font-weight: lighter;
  font-size: 2rem;
  color: white;
  font-weight: bold;
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-expense__control input:focus {
  outline: none;
}

.new-expense__actions {
  text-align: right;
}

.new-expense {
  background-color: #a892ee;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: #510674;
  border-color: #510674;
}

.new-expense button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #ddb3f8;
}

