.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}

.month-year {
  display: flex;
  align-items: center;
}

.month-year .month {
  margin-right: 1rem;
}

.month-year button {
  font: inherit;
  cursor: pointer;
  padding: 0.7rem 1rem;
  border: 1px solid #40005d;
  background-color: #f9dffe;
  border-radius: 12px;
  margin-left: 1rem;
}
