.expense-date {
  display: flex;
  padding: 1.5rem;
  color: #fff;
  flex-direction: column;
  align-items: center;
  border-radius: 12px;
  border: 1px solid #fff;
  background: rgba(0, 0, 0, 0.4);
}

.expense-date__month {
  font-weight: bold;
  font-size: 1.2rem;
}

.expense-date__year {
  font-size: 1.1rem;
}

.expense-date__day {
  font-size: 2rem;
  font-weight: 700;
  margin-top: 5px;
}
