.expenses {
  background: rgb(31, 31, 31);
  margin: 2rem auto;
  /* width: 50rem; */
  max-width: 95%;
  padding: 1rem;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}
.expense-item__date {
  font-size: 1.3rem;
  color: #fff;
}

.remove-icon {
  /* padding: 1rem; */
  border-radius: 12px;
  padding: 0rem 1.5rem;
  font-size: 2rem;
  background-color: transparent;
  outline: none;
  box-shadow: none;
  color: orange;
  cursor: pointer;
}

.expense-item__flex {
  display: flex;
  /* align-items: center; */
  justify-content: center;
  gap: 0.7rem;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item__price {
  background: purple;
  color: #fff;
  font-size: 1.7rem;
  padding: 1rem 4rem;
  border: 1px solid #fff;
  border-radius: 15px;
}

@media (max-width: 800px) {
  .expense-item__description {
    flex-direction: row;
    align-content: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
  }
}
